@charset "UTF-8";
h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

html,
body,
ul,
li,
p,
h1,
h2,
h3,
h4,
button,
input,
textarea {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif; }

a,
a:visited {
  text-decoration: none; }

.nav {
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  line-height: 16px;
  letter-spacing: 2px;
  padding-bottom: 48px; }
  .nav__list {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none; }
  .nav__item {
    padding: 8px 24px;
    margin: 0 26px; }
    .nav__item:last-child {
      padding-right: 0;
      margin-right: 0; }
    .nav__item:first-child {
      margin-left: 0; }
    .nav__item--active {
      background-color: rgba(255, 255, 255, 0.1);
      border-radius: 8px; }
  .nav__link {
    color: #fff;
    text-decoration: none;
    white-space: nowrap;
    transition: color 0.2s; }
    .nav__link:hover {
      color: #9fadc2; }

@media screen and (max-width: 600px) {
  .nav__list {
    flex-direction: column; } }

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 700px;
  /* height: 100vh; */
  background-image: url(../images/header.jpg);
  background-size: cover;
  background-position: center;
  position: relative;
  margin-bottom: 104px; }
  .header__nav {
    display: flex;
    justify-content: space-between; }
  .header__top {
    align-self: start;
    padding-top: 24px;
    width: 1020px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    line-height: 16px;
    letter-spacing: 2px;
    color: #fff; }
  .header__title {
    position: relative;
    font-size: 60px;
    margin-top: auto;
    margin-bottom: 85px;
    line-height: 60px;
    font-weight: bold;
    color: #fff; }
    .header__title::before {
      position: absolute;
      top: -16px;
      left: 0;
      content: "";
      width: 35px;
      height: 2px;
      background-color: #fff;
      border-radius: 2px; }
  .header__logo {
    background-image: url(../images/logo.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 115px;
    height: 76px; }
  .header__link {
    color: #fff;
    text-decoration: none;
    padding-bottom: 8px;
    border-bottom: 1px solid #fff;
    transition: color 0.2s; }
    .header__link:hover {
      color: #9fadc2; }

.header-wrapper {
  display: flex;
  width: 1020px;
  margin: 0 auto;
  flex-direction: column;
  justify-content: space-between; }

.section {
  width: 1020px;
  margin: 0 auto;
  color: #000; }
  .section__heading {
    margin: 0 auto 61px;
    text-align: center; }

.info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 152px; }
  .info__title {
    width: 328px;
    font-size: 48px;
    line-height: 48px;
    font-weight: bold;
    letter-spacing: -1.5px;
    color: #000;
    white-space: nowrap;
    margin: 0; }
  .info__link {
    color: #0075ff;
    text-decoration: none; }
  .info__text {
    max-width: 500px;
    font-size: 16px;
    line-height: 27px; }
    .info__text > :first-child {
      margin-top: 0; }
    .info__text > :last-child {
      margin-bottom: 0; }
    .info__text--active {
      font-size: 24px;
      line-height: 30px;
      font-weight: bold;
      color: #0075ff;
      margin-bottom: 16px; }

@media screen and (max-width: 320px) {
  .info {
    flex-direction: column; } }

.heading {
  font-size: 48px;
  line-height: 48px;
  font-weight: bold;
  letter-spacing: -1.5px;
  position: relative;
  width: max-content; }
  .heading::before, .heading::after {
    position: absolute;
    display: block;
    content: "";
    width: calc(50% - 12px);
    background-color: transparent; }
  .heading::before {
    left: 0; }
  .heading::after {
    right: 0; }
  .heading__crown {
    position: absolute;
    display: block;
    left: 0;
    right: 0;
    top: calc(100% + 8px);
    margin: auto;
    width: 23px;
    height: 16px;
    background-image: url(../images/crown.svg);
    background-repeat: no-repeat; }

.photos {
  display: flex;
  justify-content: space-between; }
  .photos__img {
    width: calc(50% - 10px);
    height: 256px;
    border-radius: 8px;
    object-fit: cover;
    object-position: center; }
  .photos__link {
    text-decoration: none;
    color: #0075ff; }

