.gallery__caption {
  margin-top: 24px;
  margin-bottom: 40px; }

.gallery__button {
  margin: 40px auto 152px;
  padding: 21px 65px; }

.button {
  box-sizing: border-box;
  display: block;
  font-size: 16px;
  line-height: 14px;
  color: #0075ff;
  font-weight: bold;
  border: 2px solid #0075ff;
  border-radius: 8px;
  cursor: pointer; }
  .button:hover {
    background-color: #0075ff;
    color: #fff; }

.consultation {
  display: flex;
  justify-content: space-between; }
  .consultation__heading {
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
    margin-bottom: 16px; }
  .consultation__text {
    width: 412px; }
  .consultation__form {
    width: 413px; }

.form {
  display: flex;
  flex-direction: column; }
  .form__item {
    display: block;
    margin-bottom: 16px; }
  .form__input, .form__button {
    width: 100%; }
  .form__button {
    text-align: center;
    padding: 21px;
    margin-bottom: 152px; }
    .form__button :hover {
      box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);
      border-radius: 8px; }

.input {
  font-size: 14px;
  line-height: 14px;
  font-weight: normal;
  color: #b9c1ce;
  background-color: #f7f8ff;
  border: 1px solid #fff;
  border-radius: 8px;
  padding: 17px;
  box-sizing: border-box; }
  .input::placeholder {
    color: inherit; }
  .input__textarea {
    height: 144px; }

.testimonials {
  display: grid;
  grid-template-columns: repeat(3, 240px);
  grid-gap: 150px;
  justify-content: center;
  margin-top: 70px;
  margin-bottom: 152px; }

@media screen and (max-width: 600px) {
  .testimonials {
    grid-template-columns: repeat(1, 280px); } }

.person {
  text-align: center; }
  .person__photo {
    width: 153px;
    height: 153px;
    border-radius: 50%; }
  .person__name {
    margin-top: 28px;
    font-size: 24px;
    line-height: 30px;
    font-weight: bold;
    color: #0075ff; }
  .person__position {
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
    margin: 4px auto; }

.contact {
  margin-top: 70px; }
  .contact__form {
    width: 413px; }
  .contact__button:hover {
    box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
    border-radius: 8px; }

.footer {
  height: 359px;
  position: relative;
  background-image: url(../images/footer.jpg);
  background-position: center;
  background-size: cover; }
  .footer__logo {
    position: absolute;
    bottom: 45px;
    left: 0;
    right: 0;
    margin: auto;
    background-image: url(../images/logo.png);
    background-position: center;
    background-size: cover;
    width: 115px;
    height: 76px; }

body {
  background: #eee;
  color: #3e3e3e;
  font-size: 16px;
  line-height: 27px; }
